.contact {
  width: 100%;
  height: 90vh;
  display: flex;
}

.contact .leftSide {
  height: 100%;
  flex: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../assets/pizza.jpeg");
}

.contact .rightSide {
  height: 100%;
  flex: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact .rightSide h1 {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: 10;
  font-size: 50px;
  margin-left: 20px;
}

form {
  display: flex;
  flex-direction: column;
  width: auto;
  height: auto;
  padding: 30px;
  justify-content: center;
}

form input {
  height: 40px;
  width: 90%;
  border: none;
  border-bottom: 1px solid #121619;
  text-align: center;
  color: black;
  margin-bottom: 30px;
}

form textarea {
  margin-top: 15px;
  height: 70px;
  width: 90%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
  text-align: center;
  resize: none;
}
input::placeholder,
textarea::placeholder {
  font-weight: bold;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
form input:focus,
textarea:focus {
  outline: none;
}

/* form label {
  margin-top: 15px;
  color: grey;
} */

form button {
  margin-top: 40px;
  margin-left: 90px;
  width: 140px;
  height: 40px;
  border: none;
  background-color: #121619;
  color: whitesmoke;
  font-size: 15px;
  cursor: pointer;
}
@media only screen and (max-width: 900px) {
  .contact {
    flex-direction: column;
  }

  input::placeholder,
  textarea::placeholder {
    font-size: 1.25rem;
  }
}
